<template>
  <DataTable
    :title="title"
    :items="users"
    :pending="pending"
    :page="page"
    :perPage="perPage"
    :totalCount="totalCount"
    @rowSelect="onRowSelect"
  >
    <template #empty>
      <div class="p-text-center">
          
      </div>
    </template>

    <Column
      field="id"
      header="#"
    />

    <Column
      field="username"
      header=" "
    />

    <Column
      field="email"
      header="E-mail"
    />

    <Column
      field="role"
      header=""
      headerClass="p-text-center"
      bodyClass="p-text-center"
    >
      <template #body="{ data: { role } }">
        <RoleView :value="role"/>
      </template>
    </Column>

    <Column
      field="group"
      header=""
      headerClass="p-text-center"
      bodyClass="p-text-center"
    >
      <template #body="{ data: { group } }">
        <GroupView
          v-if="group"
          :group="group"
        />

        <span v-else>
           
        </span>
      </template>
    </Column>
  </DataTable>
</template>

<script src="./index.js"></script>
